.backdrop.pop-up {
    position: fixed;
    top: 0;
    left: 0;
    opacity: 1;
    z-index: 2;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.2);
    -webkit-transition: opacity 500ms cubic-bezier(0.4, 0, 0.2, 1);
    transition: opacity 500ms cubic-bezier(0.4, 0, 0.2, 1);
    overflow-y: scroll;
}

.pop-up.is-hidden {
    opacity: 0;
    pointer-events: none;
    transition: opacity 500ms cubic-bezier(0.4, 0, 0.2, 1);
}

.modal-pop {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 40px;
    background-color: $secondary-bg-color;
    
        @media screen and (max-width: $mobile-min) {
            width: 94%;
          }
      
          @media screen and (min-width: $mobile) {
            width: 95%;
          }
      
          @media screen and (min-width: $tablets){
            width: 60%;
            
          }
      
          @media screen and (min-width: $desktop) {
            width: 50%;
            
          }
    }


.modal-text {
    margin-top: 0;
    margin-bottom: 44px;
    font-weight: 700;
    font-size: 32px;
    line-height: 1.16;
    text-align: center;
    letter-spacing: 0.05em;
    width: 100%;
    text-transform: uppercase;
    text-align: center;

    color: $primary-color-text;
}

.submit-btn {
    display: flex;
    justify-content: center;

    & .send-btn {
        font-family: Open Sans;
        font-style: normal;
        font-weight: 600;
        font-size: 12px;
        line-height: 1.33;
        letter-spacing: 0.09em;
        text-transform: uppercase;
        outline: transparent;

        color: $primary-color-text;

        border: 1px solid $accent-color;
        border-radius: 25px;
        padding: 12px 20px;
        width: 160px;
        height: 40px;
        background: $primary-white-color;

        &:hover, &:focus {
            color: $primary-white-color;
            background-color: $accent-color;
            transition: background-color 500ms cubic-bezier(0.4, 0, 0.2, 1);
        }
    }
}

.close-btn {
    fill: $primary-color-text;
}

.modal-button.close {
    position: absolute;
    top: 10px;
    right: 15px;

    width: 25px;
    height: 25px;
    background-color: $primary-white-color;
    border: none;
    // padding: 0;
    
    &:hover, &:focus .close-btn {
        fill: $accent-color;
    }
}